import React from 'react';
import { connect } from 'react-redux';

import GameList from './GameList';
import DirectionList from './DirectionList';
import { actionCreators } from './../reducer';

function App({ x, y, up, right, down, left }) {
  return (
      <section className="game">
        <GameList x={x} y={y} />
        <DirectionList x={x} y={y} up={up} right={right} down={down} left={left} />
      </section>
  );
}

const mapStateToProps = (state) => ({
  x: state.x,
  y: state.y,
});

const mapDispatchToProps = (dispatch) => ({
  up: () => dispatch(actionCreators.up()),
  down: () => dispatch(actionCreators.down()),
  left: () => dispatch(actionCreators.left()),
  right: () => dispatch(actionCreators.right()),
});

export default connect(mapStateToProps, mapDispatchToProps)(App);
