import React from 'react';

import './GameList.css';

class GameList extends React.PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            items: ['first', 'second', 'third', 'fourth', 'fifth', 'sixth', 'seventh', 'eighth', 'ninth'],
            startItem: 3 * props.x + props.y,
        };
    }

    render() {
        const { items, startItem } = this.state;

        return (
            <ul className="game__list">
                {items.map((item, index) => {
                    if (startItem === index) {
                        return <li className="game__item">start</li>;
                    } else if (3 * this.props.x + this.props.y === index) {
                        return <li className="game__item">finish</li>;
                    }

                    return <li className="game__item"></li>;
                })}
            </ul>
        );
    }
}

export default GameList;