import React from 'react';

import './DirectionList.css';

class DirectionList extends React.PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            items: ['first', 'second', 'third', 'fourth', 'fifth', 'sixth', 'seventh', 'eighth', 'ninth', 'tenth'],
        };
    }

    render() {
        const { items } = this.state;

        return (
            <ul className="game__direction-list">
                {items.map((item) => {
                    return <li className="game__direction-item" key={item}>{this.getDirection()}</li>;
                })}
            </ul>
        );
    }

    getDirection() {
        switch(Math.floor(Math.random() * 4)) {
            case 0:
                if (this.props.x === 0) {
                    return this.getDirection();
                }

                this.props.up();

                return 'up';

            case 1:
                if (this.props.y === 2) {
                    return this.getDirection();
                }

                this.props.right();

                return 'right';

            case 2:
                if (this.props.x === 2) {
                    return this.getDirection();
                }

                this.props.down();

                return 'down';

            case 3:
                if (this.props.y === 0) {
                    return this.getDirection();
                }

                this.props.left();

                return 'left';

            default:
                return '';
        }
    }
}

export default DirectionList;
